.blog{
    max-width: 1186px;
    margin: 0 auto;
    &__title{
        font-family: "MuliExtBold" , sans-serif;
        font-weight: 600;
        color: #f7ae3b;
        font-size: 50px;
        line-height: 40px;
        margin:100px 0 57px;
    }
    
    &__inner{    
        display:flex;
    }
    
    &__img-wrap{
        margin-right: 60px;
        margin-bottom: 195px;
        object-fit: cover;
        z-index: 5;
    }
    
    &__img{
        max-width: 100%;
    }
    
    &__article{
        width: 570px;
    }
    
    &__subtitle{
        font-family: "MuliExtBold" , sans-serif;
        font-weight: 600;
        color: #f2612d;
        font-size: 30px;
        text-transform: uppercase;
        margin-top: 125px;
        margin-bottom: 4px;
        position: relative;
        &::before{
            content:url("./../images/img/big-line.png");
            position: absolute;
            bottom: 85px;
            right: -5px;
            z-index: 1;
        }
    }
    
    &__date{
        font-family: "MuliRegul", sans-serif;
        font-weight: 400;
        color: #1e1e1e;
        font-size: 16px;
        line-height: 35px;
        
    }
    
    &__text{
        font-family: "MuliRegul", sans-serif;
        font-weight: 400;
        color: #1e1e1e;
        font-size: 17px;
        line-height: 35px;
        letter-spacing: 1px;
        padding-left: 64px;
        margin-top: 26px;
    }
    
    &__text-link{
        font-family: "MuliRegul", sans-serif;
        font-weight: 400;
        color: #1e1e1e;
        font-size: 18px;
        font-weight: 900;
        line-height: 35px;
        text-decoration: none;
        &::after{
            content:url("./../images/icon/little-arrow.png");
            padding-left: 8px;
        }
    }
}